const AFRAME = window.AFRAME;

AFRAME.registerComponent('opinion-video', {
    schema: {
        actionButton: {type: 'selector', default: ''},
        video: {type: 'selector', default: ''},
        mainVideo: {type: 'selector', default: ''},
        panelVideo: {type: 'selector'}
    },

    init: function () {

        const el = this.el;
        const data = this.data;

        this.isVideoOpen = false;

        data.actionButton.addEventListener('click', ()=>{
            // console.log('abriendo video');
            if(!this.isVideoOpen){
                data.panelVideo.setAttribute('visible', true);
                data.panelVideo.emit('open_video');
                data.video.currentTime = 0.1;
                data.video.play();
                if(data.mainVideo){
                    data.mainVideo.pause();
                }
                
            }else{
                data.panelVideo.emit('close_video');
                data.video.pause();
            }
            this.isVideoOpen = !this.isVideoOpen;
        })

        this.el.addEventListener('targetFound', event => {
            data.video.currentTime = 0.1;
        });
        this.el.addEventListener('targetLost', event => {
            data.panelVideo.emit('close_video');
            data.video.pause();
            this.isVideoOpen = false;
        });

        data.video.addEventListener("ended", (e) => {
            data.panelVideo.emit('close_video');
            data.video.pause();
            this.isVideoOpen = false;
        });


    }
});