import React, { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function ModalInformation() {
    const [open, setOpen] = useState(false);
    const [title, setTitle] = useState('');
    const [information, setInformation] = useState('');

    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const handleClose = () => {
        setOpen(false);
    };
    const openModal = (e) => {
        const new_title = e.detail.title
        const new_information = e.detail.information;

        setTitle(new_title);
        setInformation(new_information);
        setOpen(true);
    }

    useEffect(() => {
        document.addEventListener('open_information_modal', openModal)
        return () => document.removeEventListener("open_information_modal", openModal);
    }, []);


    return (
        <Dialog
            TransitionComponent={Transition}
            fullScreen={false}
            open={open}
            onClose={handleClose}
            aria-labelledby="responsive-dialog-title"
        >
            <DialogTitle id="responsive-dialog-title">
                {title}
            </DialogTitle>
            <DialogContent dividers>
                <DialogContentText>
                    {information}
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                {/* <Button autoFocus onClick={handleClose}>
                    Disagree
                </Button> */}
                <Button onClick={handleClose} autoFocus>
                    Cerrar
                </Button>
            </DialogActions>


        </Dialog>
    );
}