import React, { useEffect, useRef } from 'react';

export default ({ started, setArReady }) => {
  const sceneRef = useRef(null);

  useEffect(() => {
    const sceneEl = sceneRef.current;
    const arSystem = sceneEl.systems["mindar-image-system"];
    sceneEl.addEventListener('renderstart', () => {
      //arSystem.start(); // start AR 
    });

    sceneEl.addEventListener("arReady", (event) => {
      setArReady(true);
    });
    return () => {
      arSystem.stop();
    }
  }, []);

  useEffect(() => {
    const sceneEl = sceneRef.current;
    const arSystem = sceneEl.systems["mindar-image-system"];
    if (started) {
      arSystem.start();
    }
  }, [started]);

  return (
    <a-scene
      scene-loader
      class='interactable-ui'
      ref={sceneRef}
      mindar-image={`
            imageTargetSrc: ${process.env.PUBLIC_URL + '/assets/targets/targets.mind'};
            autoStart: false;
            uiScanning: #scanning-overlay; 
            uiError: yes;
            filterMinCF:0.001; 
            filterBeta: 100;
            warmupTolerance: 1
            missTolerance: 2"`}
      color-space="sRGB"
      renderer="colorManagement: true, physicallyCorrectLights"
      vr-mode-ui="enabled: false"
      device-orientation-permission-ui="enabled: false">

      <a-assets>
        <img
          id="card_flanax"
          src={process.env.PUBLIC_URL + '/assets/targets/flanax_550_play.png'}
        />

        <img
          id="card_flanax_fem"
          src={process.env.PUBLIC_URL + '/assets/targets/flanax_550_fem.png'}
        />

        <video
          id="asset_video_flamax"
          src={process.env.PUBLIC_URL + '/assets/videos/video_flanax_550.mp4'}
        >
        </video>

        <video
          id="asset_video_flamax_opinion"
          src={process.env.PUBLIC_URL + '/assets/videos/flanax_opinion.mp4'}
        >
        </video>

        <video
          id="asset_video_flamax_fem_opinion"
          src={process.env.PUBLIC_URL + '/assets/videos/flanax_fem_opinion.mp4'}
        >
        </video>


        {/* ICONOS */}
        <img id="asset_button_information" src={process.env.PUBLIC_URL + '/assets/images/boton_informacion.png'} />
        <img id="asset_button_indications" src={process.env.PUBLIC_URL + '/assets/images/boton_indicaciones.png'} />
        <img id="asset_button_opinion" src={process.env.PUBLIC_URL + '/assets/images/boton_medico.png'} />
        <img id="asset_button_discount" src={process.env.PUBLIC_URL + '/assets/images/boton_cupon.png'} />
        <img id="asset_button_present" src={process.env.PUBLIC_URL + '/assets/images/boton_regalo.png'} />
        {/* ICONOS */}

      </a-assets>

      <a-camera
        position="0 0 0"
        look-controls="enabled: false"
        cursor="fuse: false; rayOrigin: mouse;"
        raycaster="far: 10000; objects: .clickable">
      </a-camera>

      <a-entity mindar-image-target="targetIndex: 0"
        mind-target-video="video: #asset_video_flamax; panelVideo: #video-flamax"
        opinion-video="actionButton: #flanax-opinion-button; 
          video:#asset_video_flamax_opinion; 
          mainVideo:#asset_video_flamax; 
          panelVideo:#video-flamax-opinion;
          ">

        <a-video id="video-flamax" src="#asset_video_flamax"
          width="1" height="0.6908"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;">
        </a-video>

        <a-plane id="plane-flanax" visible="false" src="#card_flanax" width="1" height="0.6908" class="clickable"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;">
        </a-plane>

        <a-video id="video-flamax-opinion"
          position="0 0 -0.01"
          visible="false"
          animation__spawn="property: position; to: 0 0.63 -0.01; dur: 800; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: open_video;"
          animation__close="property: position; to: 0 0 -0.01; dur: 800; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close_video;"
          src="#asset_video_flamax_opinion"
          width="1" height="0.5625" >
        </a-video>

        <a-image
          id="flanax-information-button"
          src="#asset_button_information"
          transparent="true"
          position="-1 0.36 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          go-to-url="url: https://www.flanax.com.mx/productos-alivio-del-dolor-muscular/flanax-550"
          class="clickable">
        </a-image>

        <a-image
          id="flanax-contraindications-button"
          src="#asset_button_indications"
          transparent="true"
          position="-1 0.12 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          open-modal-information="
            title: Indicaciones de uso;
            information: 1 sola toma al día para adultos y niños mayores de 12 años.;
          "
          class="clickable">
        </a-image>

        <a-image
          id="flanax-opinion-button"
          src="#asset_button_opinion"
          transparent="true"
          position="-1 -0.12 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          class="clickable">
        </a-image>


        <a-image
          id="flanax-discount-button"
          src="#asset_button_discount"
          transparent="true"
          position="-1 -0.36 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          open-modal-discount
          class="clickable">
        </a-image>

      </a-entity>




      <a-entity mindar-image-target="targetIndex: 1"
        mind-target-plane
        opinion-video="actionButton: #flanax-fem-opinion-button; 
          video:#asset_video_flamax_fem_opinion; 
          mainVideo:#none; 
          panelVideo:#video-flamax-fem-opinion;
          ">

        <a-video id="video-flamax-fem-opinion"
          position="0 0 -0.01"
          visible="false"
          animation__spawn="property: position; to: 0 0.68 -0.01; dur: 800; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: open_video;"
          animation__close="property: position; to: 0 0 -0.01; dur: 800; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: close_video;"
          src="#asset_video_flamax_fem_opinion"
          width="1" height="0.5625" >
        </a-video>

        <a-plane id="image-fem-flamax" src="#card_flanax_fem"
          height="0.8" width="1"
          animation__spawn="property: components.material.material.opacity; from: 0; to: 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
        >
        </a-plane>

        <a-image
          id="flanax-fem-information-button"
          src="#asset_button_information"
          transparent="true"
          position="-1 0.36 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          go-to-url="url: https://www.flanax.com.mx/dolor-inflamacion/dolores-menstruales"
          class="clickable">
        </a-image>

        <a-image
          id="flanax-fem-contraindications-button"
          src="#asset_button_indications"
          transparent="true"
          position="-1 0.12 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          open-modal-information="
            title: Indicaciones de uso;
            information: 1 sola toma al día para aliviar el dolor e inflamación causados por cólicos menstruales en mujeres y niñas mayores de 12 años.
          "
          class="clickable">
        </a-image>

        <a-image
          id="flanax-fem-opinion-button"
          src="#asset_button_opinion"
          transparent="true"
          position="-1 -0.12 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          class="clickable">
        </a-image>

        <a-image
          id="flanax-fem-discount-button"
          src="#asset_button_present"
          transparent="true"
          position="-1 -0.36 0.1"
          rotation="0 0 0"
          scale="0 0 0"
          animation__spawn="property: scale; from:0 0 0; to: 1 1 1; dur: 500; easing: easeInOutQuad;elasticity:1000; loop: false; dir: alternate; startEvents: spawn;"
          animation__loop="property: scale; to: 1.1 1.1 1.1; dur: 800; easing: easeInOutQuad; loop: true; dir: alternate; startEvents: loop_animation;"
          height="0.2" width="0.8576"
          go-to-url="url: https://hashting.cash/flanaxfem/FLANAXFEMDEMOMXWCT?language=es"
          class="clickable">
        </a-image>

      </a-entity>
    </a-scene>
  )
}
