import React, { useEffect } from 'react';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import ViewInArIcon from '@mui/icons-material/ViewInAr';
import LoadingButton from '@mui/lab/LoadingButton';
import { Box, Button, Container, Grid, Link, TextField, Typography } from '@mui/material';

// Componentes
import Copyright from '../Copyright/Copyright';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function StartModal({ sceneLoaded, arReady, setStarted }) {
    const [open, setOpen] = React.useState(true);
    const [loading, setLoading] = React.useState(false);

    useEffect(() => {
        if (arReady) {
            setOpen(false);
        }
    }, [arReady]);

    const handleClose = () => {
        setLoading(true);
        setStarted(true);
    };

    return (

        <Dialog
            fullScreen
            open={open}
            onClose={handleClose}
            TransitionComponent={Transition}
            PaperProps={{
                style: {
                    backgroundColor: 'white',
                    boxShadow: 'none',
                },
            }}
        >
            <Container maxWidth="sm"
                sx={{
                    display: 'flex',
                    height: '100%',
                    width: '90%',
                    flexDirection: 'column',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    padding:'2rem 0 0 0'
                }}>
                <img className="o-logo-header"
                    src={process.env.PUBLIC_URL + '/assets/images/LogoFlanaxNuevo.png'}
                    height="80" alt="" />

                <Box sx={{ py: 1 }}>

                    <Typography
                        align="center"
                        color="textSecondary"
                        variant="body1"
                    >
                        Bienvenido a la experiencia de realidad aumentada de Flanax. 
                        Por favor presiona en "Comenzar" y acerca la cámara al producto de 
                        Flanax del cual quieras conocer
                    </Typography>

                    <LoadingButton
                        // className='interactable-ui'
                        disabled={!sceneLoaded}
                        sx={{ mt: 3}}
                        fullWidth
                        size='large'
                        loading={loading}
                        loadingPosition="start"
                        startIcon={<ViewInArIcon />}
                        variant="outlined"
                        onClick={handleClose}
                    >
                        {sceneLoaded? 'Comenzar' : 'Cargando...'}
                    </LoadingButton>
                </Box>

                {/* <br></br> */}
                <Box sx={{ py: 1, display:'flex', flexDirection:'column', alignItems:'center' }}>
                    <img className="o-logo-header"
                        src={process.env.PUBLIC_URL + '/assets/images/Logo_Bayer.svg.png'}
                        height="50" alt=""
                        style={{ marginBottom: '1rem' }} />

                    <Copyright></Copyright>
                </Box>

            </Container>
        </Dialog>
    );
}