AFRAME.registerComponent('mind-target-video', {
    schema: {
        video: { type: 'selector', default: '' },
        panelVideo: { type: 'selector' }
    },

    init: function () {

        const el = this.el;
        const data = this.data;
        let isfirstRecognicion = true;
        let visitRegistered = false;

        const panelVideo = document.querySelector('#video-flamax');
        const panelImage = document.querySelector('#plane-flanax');
        const buttons = [
            document.querySelector('#flanax-information-button'),
            document.querySelector('#flanax-contraindications-button'),
            document.querySelector('#flanax-opinion-button'),
            document.querySelector('#flanax-discount-button'),
        ];

        panelImage.addEventListener('click', () => {
            panelVideo.setAttribute('visible', true);
            panelImage.setAttribute('visible', false);
            panelVideo.emit('spawn');
            data.video.currentTime = 0.1
            data.video.play();
        })

        data.video.addEventListener("ended", (e) => {
            panelVideo.setAttribute('visible', false);
            panelImage.setAttribute('visible', true);

            buttons.forEach(button => {
                if (button) {
                    button.emit('spawn');
                    setTimeout(() => {
                        button.emit('loop_animation')
                    }, 800);
                }
            });

            isfirstRecognicion = false;
        });

        this.el.addEventListener('targetFound', event => {
            this.el.setAttribute('visible', true);
            document.dispatchEvent(closeInstructionsEvent);

            try {
                panelVideo.setAttribute('visible', true);
                panelImage.setAttribute('visible', false);
                panelVideo.emit('spawn');
                data.video.currentTime = 0.1
                data.video.play();
            } catch (error) {
                console.error(error);
                panelVideo.setAttribute('visible', false);
                panelImage.setAttribute('visible', true);
            }

            

            if (!isfirstRecognicion) {
                buttons.forEach(button => {
                    if (button) {
                        button.emit('spawn');
                        setTimeout(() => {
                            button.emit('loop_animation')
                        }, 800);
                    }
                });
            }

            if (!visitRegistered) {
                registerVisit();
                visitRegistered = true;
            }

            // data.panelVideo.classList.add("clickable");
        });
        this.el.addEventListener('targetLost', event => {
            data.video.pause();
            this.el.setAttribute('visible', false);
            // data.panelVideo.classList.remove("clickable");
        });

    }
});




AFRAME.registerComponent('mind-target-plane', {

    init: function () {

        const panelImage = document.querySelector('#image-fem-flamax');

        const buttons = [
            document.querySelector('#flanax-fem-information-button'),
            document.querySelector('#flanax-fem-contraindications-button'),
            document.querySelector('#flanax-fem-opinion-button'),
            document.querySelector('#flanax-fem-discount-button'),
        ];

        this.el.addEventListener('targetFound', event => {
            this.el.setAttribute('visible', true);
            document.dispatchEvent(closeInstructionsEvent);
            panelImage.emit('spawn');
            buttons.forEach(button => {
                if (button) {
                    button.emit('spawn');
                    setTimeout(() => {
                        button.emit('loop_animation')
                    }, 800);
                }
            });
        });
        this.el.addEventListener('targetLost', event => {
            this.el.setAttribute('visible', false);
        });

    }
});